$light-bg-color: white;
$light-highlight-color: #eeeeee;
$main-color: #0c9;
$dark-highlight-color: #393e46;

.compodetail {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 24px 24px;
}

.hideThisShit {
    display: none;
}

.head__bikename {
    margin-bottom: 12px;
}

.compodetail__upper {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    margin-bottom: 24px;
}

.compodetail__head {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.compodetail__km-hours {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
    width: 100%;
}

.km-hours {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 5%;
    width: 130px;
    padding: 12px;
    background-color: $light-highlight-color;
    border-radius: 5px;
    box-shadow: 5px 5px 5px 0px rgba(209, 205, 209, 1);
}

.separator-small {
    width: 95%;
    margin: auto;
    border-bottom: 0.8px solid $main-color;
    margin-bottom: 10px;
}

.km-hours__title {
    font-weight: bold;
    margin-bottom: 5px;
}

.compodetail__components {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.compodetail__details {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
}

.card-decoration {
    height: inherit;
    width: 2%;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    background-color: $main-color;
}

.details__content {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 88%;
    background-color: $light-highlight-color;
    border-radius: 5px;
    padding: 12px;
    box-shadow: 5px 5px 5px 0px rgba(209, 205, 209, 1);
    border-left: 5px solid $main-color;
}

.details__type-img {
    height: 50px;
    width: 50px;
    margin-right: 12px;
}

.details__info {
    display: flex;
    flex-direction: column;
}

.details__info p {
    padding: 5px 0px;
}

.info__type {
    font-weight: bold;
}

.compodetail__reset {
    width: 90%;
    height: 40px;
    background-color: $main-color;
    border: none;
    border-radius: 5px;
    color: black;
    font-size: 18px;
    cursor: pointer;
    margin-top: 32px;
    margin-bottom: 32px;
}

@media only screen and (min-width: 500px) {
    .compodetail {
        padding: 48px;
    }
}

@media only screen and (min-width: 900px) {
    .destkop {
        display: flex;
    }

    .mobile {
        display: none;
    }

    .general-container {
        display: flex;
        flex-direction: row;
        padding: 32px 48px;
    }

    .compodetail {
        padding: 0 64px;
        width: 100%;
        justify-content: flex-start;
    }

    .compodetail__head {
        align-items: center;
        width: 100%;
        padding: 12px 24px;
    }

    .head__container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }

    .compodetail__reset {
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: 200px;
        height: 45px;
        font-weight: bold;
        color: black;
        transition: all 0.2s ease-in-out;
    }

    .compodetail__reset:hover {
        color: whitesmoke;
        transform: scale(1.08);
    }

    .km-hours {
        margin: unset;
    }
}
